import { clerkMiddleware, auth, clerkClient } from "@clerk/nextjs/server";
import { NextResponse } from "next/server";

export default clerkMiddleware(async (req) => {
  const { userId } = auth(); // Only userId and sessionId are available here
  const isAdminRoute = req.nextUrl.pathname.startsWith("/admin");

  if (isAdminRoute) {
    if (!userId) {
      return NextResponse.redirect(new URL("/unauthorized", req.url));
    }

    // Fetch full user data to check role
    const user = await clerkClient.users.getUser(userId);
    const isAdmin = user.publicMetadata.role === "admin";

    if (!isAdmin) {
      return NextResponse.redirect(new URL("/unauthorized", req.url));
    }
  }

  return NextResponse.next();
});

export const config = {
  matcher: ["/admin/:path*"],
};
